const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Democracy',
    public: {
      proposalCount: section.method('proposalCount')({
        description: 'The number of (public) proposals that have been made so far',
        key: 'dem:ppc',
        params: [],
        type: 'PropIndex'
      }),
      proposals: section.method('proposals')({
        description: 'The public proposals. Unsorted.',
        key: 'dem:pub',
        params: [],
        type: [['PropIndex', 'Proposal', 'AccountId']]
      }),
      depositOf: section.method('depositOf')({
        description: 'Those who have locked a deposit',
        key: 'dem:dep:',
        params: [param('index', 'PropIndex')],
        type: ['Balance', ['AccountId']]
      }),
      launchPeriod: section.method('launchPeriod')({
        description: 'How often (in blocks) new public referenda are launched',
        key: 'dem:lau',
        params: [],
        type: 'BlockNumber'
      }),
      minimumDeposit: section.method('minimumDeposit')({
        description: 'The minimum amount as a deposit for a public referendum',
        key: 'dem:lau',
        params: [],
        type: 'Balance'
      }),
      votingPeriod: section.method('votingPeriod')({
        description: 'How often (in blocks) to check for new votes',
        key: 'dem:per',
        params: [],
        type: 'BlockNumber'
      }),
      referendumCount: section.method('referendumCount')({
        description: 'The number of referendums started so far',
        key: 'dem:rco',
        params: [],
        type: 'ReferendumIndex'
      }),
      nextTally: section.method('nextTally')({
        description: 'The next referendum index that should be tallied',
        key: 'dem:nxt',
        params: [],
        type: 'ReferendumIndex'
      }),
      referendumInfoOf: section.method('referendumInfoOf')({
        description: 'Details for a specific referendum',
        key: 'dem:pro:',
        params: [param('referendum', 'ReferendumIndex')],
        type: ['BlockNumber', 'Proposal', 'VoteThreshold']
      }),
      votersFor: section.method('votersFor')({
        description: 'Get the voters for the current proposal',
        key: 'dem:vtr:',
        params: [param('referendum', 'ReferendumIndex')],
        type: ['AccountId']
      }),
      voteOf: section.method('voteOf')({
        description: 'Get the vote for Account',
        key: 'dem:vot:',
        params: [param('referendum', 'ReferendumIndex'), param('who', 'AccountId')],
        type: 'bool'
      })
    }
  });
};