const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = (name, index) => {
  const section = createSection(name, index);
  return section({
    description: 'Staking',
    public: {
      transfer: section.method('transfer', 0)({
        description: 'Transfer',
        params: [param('recipient', 'AccountId'), param('value', 'Balance')],
        type: []
      }),
      stake: section.method('stake', 1)({
        description: 'Stake',
        params: [],
        type: []
      }),
      unstake: section.method('unstake', 2)({
        description: 'Unstake',
        params: [],
        type: []
      })
    },
    private: {
      setSessionsPerEra: section.method('setSessionsPerEra', 0)({
        description: 'Set sessions per era',
        params: [param('sessions', 'BlockNumber')],
        type: []
      }),
      setBondingDuration: section.method('setBondingDuration', 1)({
        description: 'Set bonding duration',
        params: [param('duration', 'BlockNumber')],
        type: []
      }),
      setValidatorCount: section.method('setValidatorCount', 2)({
        description: 'Set validator count',
        params: [param('count', 'u32')],
        type: []
      }),
      forceNewEra: section.method('forceNewEra', 3)({
        description: 'Force new era',
        params: [],
        type: []
      })
    }
  });
};