const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Consensus',
    public: {
      authorityAt: section.method('authorityAt')({
        description: 'Authority at a specific index',
        isUnhashed: true,
        key: ':auth:',
        params: [param('index', 'u32')],
        type: 'AccountId'
      }),
      authorityCount: section.method('authorityCount')({
        description: 'The number of authorities',
        isUnhashed: true,
        key: ':auth:len',
        params: [],
        type: 'u32'
      }),
      code: section.method('code')({
        description: 'The current runtime code',
        isUnhashed: true,
        key: ':code',
        params: [],
        type: 'Code'
      })
    }
  });
};