const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = (name, index) => {
  const section = createSection(name, index);
  return section({
    description: 'Session',
    public: {
      setKey: section.method('setKey', 0)({
        description: 'Set session key',
        params: [param('key', 'SessionKey')],
        type: []
      })
    },
    private: {
      setLength: section.method('setLength', 0)({
        description: 'Set session length',
        params: [param('length', 'u64')],
        type: []
      }),
      forceNewSession: section.method('forceNewSession', 1)({
        description: 'Force new session',
        params: [],
        type: []
      })
    }
  });
};