const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = (name, index) => {
  const section = createSection(name, index);
  return section({
    description: 'Democracy',
    public: {
      propose: section.method('propose', 0)({
        description: 'Propose',
        params: [param('proposal', 'Proposal'), param('value', 'Balance')],
        type: []
      }),
      second: section.method('second', 1)({
        description: 'Second',
        params: [param('proposal', 'u32')],
        type: []
      }),
      vote: section.method('vote', 2)({
        description: 'Vote',
        params: [param('referendumIndex', 'u32'), param('vote', 'bool')],
        type: []
      })
    },
    private: {
      startReferendum: section.method('startReferendum', 0)({
        description: 'Start referendum',
        params: [param('proposal', 'Proposal'), param('voteThreshold', 'VoteThreshold')],
        type: []
      }),
      cancelReferendum: section.method('cancelReferendum', 1)({
        description: 'Cancel referendum',
        params: [param('referendumIndex', 'u32')],
        type: []
      })
    }
  });
};