const createSection = require('@polkadot/params/section');
/**
  @summary Methods to retrieve system info.
*/


module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Methods to retrieve system info',
    public: {
      chain: section.method('chain')({
        description: 'Retrieves the chain',
        params: [],
        type: 'String'
      }),
      name: section.method('name')({
        description: 'Retrieves the node name',
        params: [],
        type: 'String'
      }),
      version: section.method('version')({
        description: 'Retrieves the version of the node',
        params: [],
        type: 'String'
      })
    }
  });
};