const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Session management information',
    public: {
      currentIndex: section.method('currentIndex')({
        description: 'Current index of the session',
        key: 'ses:ind',
        params: [],
        type: 'BlockNumber'
      }),
      lastSessionChange: section.method('lastSessionChange')({
        description: 'Block at which the session length last changed',
        key: 'ses:llc',
        params: [],
        type: 'BlockNumber'
      }),
      nextKeyFor: section.method('nextKeyFor')({
        description: 'The next key for a given validator',
        key: 'ses:nxt:',
        params: [param('who', 'AccountId')],
        type: 'SessionKey'
      }),
      nextLength: section.method('nextLength')({
        description: 'The next session length',
        key: 'ses:nln',
        params: [],
        type: 'BlockNumber'
      }),
      length: section.method('length')({
        description: 'Current length of the session',
        key: 'ses:len',
        params: [],
        type: 'BlockNumber'
      }),
      validators: section.method('validators')({
        description: 'Current validators',
        key: 'ses:val',
        params: [],
        type: ['AccountId']
      })
    }
  });
};