const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Council voting',
    public: {
      cooloffPeriod: section.method('cooloffPeriod')({
        description: 'The coolof period',
        key: 'cov:cooloff',
        params: [],
        type: 'BlockNumber'
      }),
      votingPeriod: section.method('votingPeriod')({
        description: 'The voting period',
        key: 'cov:period',
        params: [],
        type: 'BlockNumber'
      }),
      proposals: section.method('proposals')({
        description: 'The current proposals',
        key: 'cov:prs',
        params: [],
        type: [['BlockNumber', 'Hash']]
      }),
      proposalOf: section.method('proposalOf')({
        description: 'The proposal by hash',
        key: 'cov:pro',
        params: [param('hash', 'Hash')],
        type: 'Proposal'
      }),
      proposalVoters: section.method('proposalVoters')({
        description: 'Voters on a proposal',
        key: 'cov:voters:',
        params: [param('hash', 'Hash')],
        type: ['AccountId']
      }),
      councilVoteOf: section.method('councilVoteOf')({
        description: 'Council votes on proposal',
        key: 'cov:vote:',
        params: [param('hash', 'Hash'), param('who', 'AccountId')],
        type: 'bool'
      }),
      vetoedProposal: section.method('vetoedProposal')({
        description: 'Council vetos on a proposal',
        key: 'cov:veto:',
        params: [param('hash', 'Hash')],
        type: ['BlockNumber', 'AccountId']
      })
    }
  });
};