const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = (name, index) => {
  const section = createSection(name, index);
  return section({
    description: 'Council Voting',
    public: {
      propose: section.method('propose', 0)({
        description: 'Propose',
        params: [param('proposal', 'Proposal')],
        type: []
      }),
      vote: section.method('vote', 1)({
        description: 'Vote',
        params: [param('proposal', 'Hash'), param('approve', 'bool')],
        type: []
      }),
      veto: section.method('veto', 2)({
        description: 'Veto',
        params: [param('proposal', 'Hash')],
        type: []
      })
    },
    private: {
      setCooloffPeriod: section.method('setCooloffPeriod', 0)({
        description: 'Set cooloff period',
        params: [param('blocks', 'BlockNumber')],
        type: []
      }),
      setVotingPeriod: section.method('setVotingPeriod', 1)({
        description: 'Set voting period',
        params: [param('blocks', 'BlockNumber')],
        type: []
      })
    }
  });
};