const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Council',
    public: {
      candidacyBond: section.method('candidacyBond')({
        description: 'How much should be locked up in order to submit candidacy',
        key: 'cou:cbo',
        params: [],
        type: 'Balance'
      }),
      votingBond: section.method('votingBond')({
        description: 'How much should be locked up in order to be able to submit votes',
        key: 'cou:vbo',
        params: [],
        type: 'Balance'
      }),
      persentSlashPerVoter: section.method('persentSlashPerVoter')({
        description: 'The punishment, per voter, if you provide an invalid presentation',
        key: 'cou:pss',
        params: [],
        type: 'Balance'
      }),
      carryCount: section.method('carryCount')({
        description: 'How many runners-up should have their approvals persist until the next vote',
        key: 'cou:cco',
        params: [],
        type: 'u32'
      }),
      presentationDuration: section.method('presentationDuration')({
        description: 'How long to give each top candidate to present themselves after the vote ends',
        key: 'cou:pdu',
        params: [],
        type: 'BlockNumber'
      }),
      inactiveGracePeriod: section.method('inactiveGracePeriod')({
        description: 'How many votes need to go by after a last vote',
        key: 'cou:vgp',
        params: [],
        type: 'VoteIndex'
      }),
      votingPeriod: section.method('votingPeriod')({
        description: 'How often (in blocks) to check for new votes',
        key: 'cou:per',
        params: [],
        type: 'BlockNumber'
      }),
      termDuration: section.method('termDuration')({
        description: 'How long each position is active for',
        key: 'cou:trm',
        params: [],
        type: 'BlockNumber'
      }),
      desiredSeats: section.method('desiredSeats')({
        description: 'The number of desired seats',
        key: 'cou:sts',
        params: [],
        type: 'u32'
      }),
      activeCouncil: section.method('activeCouncil')({
        description: 'The current council',
        key: 'cou:act',
        params: [],
        type: [['AccountId', 'BlockNumber']]
      }),
      voteCount: section.method('voteCount')({
        description: 'The total number of votes that have happened or are in progress',
        key: 'cou:vco',
        params: [],
        type: 'VoteIndex'
      }),
      approvalsOf: section.method('approvalsOf')({
        description: 'The last cleared vote index that this voter was last active at',
        key: 'cou:apr',
        params: [param('who', 'AccountId')],
        type: ['bool']
      }),
      registerInfoOf: section.method('registerInfoOf')({
        description: 'The vote index and list slot that the candidate was registered',
        key: 'cou:reg',
        params: [param('who', 'AccountId')],
        type: [['VoteIndex', 'u32']]
      }),
      lastActiveOf: section.method('lastActiveOf')({
        description: 'The last cleared vote index that this voter was last active at',
        key: 'cou:lac',
        params: [param('who', 'AccountId')],
        type: 'VoteIndex'
      }),
      voters: section.method('voters')({
        description: 'The present voter list',
        key: 'cou:vrs',
        params: [],
        type: ['AccountId']
      }),
      candidateCount: section.method('candidateCount')({
        description: 'Number of candidates',
        key: 'cou:cnc',
        params: [],
        type: 'u32'
      }),
      candidates: section.method('candidates')({
        description: 'The present candidate lis',
        key: 'cou:can',
        params: [],
        type: ['AccountId']
      }),
      nextFinalise: section.method('nextFinalise')({
        description: 'The accounts holding the seats that will become free',
        key: 'cou:nxt',
        params: [],
        type: ['BlockNumber', 'u32', ['AccountId']]
      }),
      snapshotedStakes: section.method('snapshotedStakes')({
        description: 'The balances',
        key: 'cou:sss',
        params: [],
        type: ['Balance']
      }),
      leaderboard: section.method('leaderboard')({
        description: 'Get the leaderboard if we;re in the presentation phase',
        key: 'cou:win',
        params: [],
        type: [['Balance', 'AccountId']]
      })
    }
  });
};