const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Timestamp related entries',
    public: {
      current: section.method('current')({
        description: 'The current timestamp',
        key: 'tim:val',
        params: [],
        type: 'Timestamp'
      }),
      didUpdate: section.method('didUpdate')({
        description: 'Did the timestamp update',
        key: 'tim:did',
        params: [],
        type: 'bool'
      })
    }
  });
};