const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');
/**
  @summary Query the state and state storage.
*/


module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Query of state',
    public: {
      call: section.method('call')({
        description: 'Perform a call to a builtin on the chain',
        params: [param('method', 'String'), param('data', 'Bytes')],
        type: 'Bytes'
      }),
      callAt: section.method('callAt')({
        description: 'Perform a call to a builtin on the chain (At block)',
        params: [param('method', 'String'), param('data', 'Bytes'), param('block', 'Hash')],
        type: 'Bytes'
      }),
      getStorage: section.method('getStorage')({
        description: 'Retrieves the storage for a key',
        params: [param('key', 'Bytes')],
        type: 'Bytes'
      }),
      getStorageAt: section.method('getStorageAt')({
        description: 'Retrieves the storage for a key at a specific block',
        params: [param('key', 'Bytes'), param('block', 'Hash')],
        type: 'Bytes'
      }),
      getStorageHash: section.method('getStorageHash')({
        description: 'Retrieves the storage hash',
        params: [param('key', 'Bytes')],
        type: 'Hash'
      }),
      getStorageHashAt: section.method('getStrorageHashAt')({
        description: 'Retrieves the storage hash at a specific block',
        params: [param('key', 'Bytes'), param('block', 'Hash')],
        type: 'Hash'
      }),
      getStorageSize: section.method('getStorageSize')({
        description: 'Retrieves the storage size',
        params: [param('key', 'Bytes')],
        type: 'u64'
      }),
      getStorageSizeAt: section.method('getStorageSizeAt')({
        description: 'Retrieves the storage size at a specific block',
        params: [param('key', 'Bytes'), param('block', 'Hash')],
        type: 'u64'
      })
    }
  });
};