const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'System',
    public: {
      blockHashAt: section.method('blockHashAt')({
        description: 'The blockHash for a specific number',
        key: 'sys:old',
        params: [param('blockNumber', 'BlockNumber')],
        type: 'Hash'
      }),
      blockNumber: section.method('blockNumber')({
        description: 'The current block number being processed',
        key: 'sys:num',
        params: [],
        type: 'BlockNumber'
      }),
      digest: section.method('digest')({
        description: 'The digest for the current block',
        key: 'sys:dig',
        params: [],
        type: 'Digest'
      }),
      extrinsicData: section.method('extrinsicData')({
        description: 'The data associated with an extrinsic',
        key: 'sys:xtd',
        params: [param('extrinsic', 'u32')],
        type: 'Bytes'
      }),
      extrinsicIndex: section.method('extrinsicIndex')({
        description: 'The extrinsics index',
        key: 'sys:xti',
        params: [],
        type: 'u32'
      }),
      extrinsicsRoot: section.method('extrinsicsRoot')({
        description: 'The extrinsicsRoot for the current block',
        key: 'sys:txr',
        params: [],
        type: 'Hash'
      }),
      accountIndexOf: section.method('accountIndexOf')({
        description: 'The index of the given account',
        key: 'sys:non',
        params: [param('who', 'AccountId')],
        type: 'Index'
      }),
      parentHash: section.method('parentHash')({
        description: 'The parentHash for the current block',
        key: 'sys:pha',
        params: [],
        type: 'Hash'
      }),
      randomSeed: section.method('randomSeed')({
        description: 'The random seed',
        key: 'sys:rnd',
        params: [],
        type: 'Hash'
      })
    }
  });
};