const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Staking',
    public: {
      freeBalanceOf: section.method('freeBalanceOf')({
        description: 'The balance of a given account',
        key: 'sta:bal:',
        params: [param('who', 'AccountId')],
        type: 'Balance'
      }),
      bondageOf: section.method('bondageOf')({
        key: 'sta:bon:',
        description: 'The block at which the account becomes liquid',
        params: [param('who', 'AccountId')],
        type: 'Balance'
      }),
      codeOf: section.method('codeOf')({
        key: 'sta:cod:',
        description: 'The code associated with an account',
        params: [param('who', 'AccountId')],
        type: 'Bytes'
      }),
      transactionFee: section.method('transactionFee')({
        description: 'The fee to be paid for making a transaction',
        key: 'sta:fee',
        params: [],
        type: 'Balance'
      }),
      bondingDuration: section.method('bondingDuration')({
        description: 'The length of the bonding duration in eras',
        key: 'sta:loc',
        params: [],
        type: 'BlockNumber'
      }),
      currentEra: section.method('currentEra')({
        description: 'The current era index',
        key: 'sta:era',
        params: [],
        type: 'BlockNumber'
      }),
      reservedBalanceOf: section.method('reservedBalanceOf')({
        description: 'The amount of a given account that is reserved',
        key: 'sta:lbo:',
        params: [param('who', 'AccountId')],
        type: 'Balance'
      }),
      intentions: section.method('intentions')({
        description: 'All the accounts with a desire to stake',
        key: 'sta:wil:',
        params: [],
        type: ['AccountId']
      }),
      lastEraLengthChange: section.method('lastEraLengthChange')({
        description: 'The block number at which the era length last changed',
        key: 'sta:lec',
        params: [],
        type: 'BlockNumber'
      }),
      nextSessionsPerEra: section.method('nextSessionsPerEra')({
        description: 'The next value of sessions per era',
        key: 'sta:nse',
        params: [],
        type: 'BlockNumber'
      }),
      sessionsPerEra: section.method('sessionsPerEra')({
        description: 'The length of a staking era in sessions',
        key: 'sta:spe',
        params: [],
        type: 'BlockNumber'
      }),
      storageOf: section.method('storageOf')({
        description: 'The storage items associated with an account/key',
        key: 'sta:sto:',
        params: [param('who', 'AccountId')],
        type: 'Bytes'
      }),
      totalStake: section.method('totalStake')({
        description: 'The total amount of stake on the system',
        key: 'sta:tot',
        params: [],
        type: 'Balance'
      }),
      validatorCount: section.method('validatorCount')({
        description: 'The number of validators',
        key: 'sta:vac',
        params: [],
        type: 'u32'
      })
    }
  });
};