const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');

module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Governbance (deprecated)',
    isDeprecated: true,
    public: {
      approvalsOf: section.method('approvalsOf')({
        description: 'Approvals for a specific account',
        isDeprecated: true,
        key: 'gov:app:',
        params: [param('who', 'AccountId')],
        type: 'Bytes'
      }),
      approvalsRatio: section.method('approvalsRatio')({
        description: 'The apprivals ratio',
        isDeprecated: true,
        key: 'gov:apr',
        params: [],
        type: 'u32'
      }),
      currentProposal: section.method('currentProposal')({
        description: 'The current proposal',
        isDeprecated: true,
        key: 'gov:pro',
        params: [],
        type: 'Bytes'
      })
    }
  });
};