const param = require('@polkadot/params/param');

const createSection = require('@polkadot/params/section');
/**
  @summary Methods to work with authors.
*/


module.exports = name => {
  const section = createSection(name);
  return section({
    description: 'Authoring of network items',
    public: {
      submitExtrinsic: section.method('submitExtrinsic')({
        isSigned: true,
        description: 'Submit a fully formatted extrinsic for block inclusion',
        params: [param('extrinsic', 'Bytes')],
        type: 'Header'
      })
    }
  });
};